.login {
  display: flex;
  justify-content: center;
  .submit {
    width: 100%;
    margin: 0;
    background-color: $secondary;
    color: $white;
    transition: 0.3s;
  }
  .submit:hover {
    background-color: $blush;
    color: $white;
  }
  form {
    width: 85%;
  }
  input {
    width: 100%;
    height: 5vh;
    font-size: $font-s;
    font-family: $sans-serif;
    margin-top: $padding-s;
    margin-bottom: $padding-s;
    border: 1px solid $secondary;
    padding-left: 5px;
  }
  label {
    font-family: $sans-serif;
    margin-bottom: $padding-m;
  }
  .login-info {
    color: $secondary;
    font-size: $font-xs;
    text-align: right;
  }
  p {
    padding-top: $padding-xs;
  }
}

@media (min-width: 768px) {
  .login form {
    width: 70%;
  }
}

@media (min-width: $max-l) {
  .banner {
    background: #ac9b8c;
    width: 100%;
  }
  .login form {
    width: 40%;
  }
}

@media (min-width: 1250px) {
  .login form {
    width: 35%;
  }
}

@media (min-width: 1500px) {
  .login form {
    width: 35%;
  }
}
